interface Props {
  userAgent: string;
}

const Home = ({ userAgent }): React.FC<Props> => (
  <h1>Hello world! - user agent: {userAgent}</h1>
);

Home.getInitialProps = async ({ req }) => {
  const userAgent: string = req
    ? req.headers["user-agent"]
    : navigator.userAgent;
  return { userAgent };
};

export default Home;
